var faker = require('./faker');

var getPriority = function getPriority(chaosDefinitions) {
  var rand = Math.random();
  var frequencies = {
    low: 0.2,
    medium: 0.4,
    high: 1
  };
  var validFrequencies = Object.keys(frequencies).filter(function (f) {
    return !!chaosDefinitions[f].length;
  });
  var frequency = validFrequencies.reduce(function (prev, current) {
    if (prev) return prev;
    return rand < frequencies[current] ? current : null;
  }, null);
  return frequency ? frequency : validFrequencies[0];
};

var randomItem = function randomItem(arr) {
  return arr[Math.floor(Math.random() * arr.length)];
};

module.exports = function (chaosDefinitions, userOptions) {
  var defaultOptions = {
    delay: 2000
  };
  var options = Object.assign({}, defaultOptions, userOptions);
  var eventHandlers = {};

  var emitRandomEvent = function emitRandomEvent() {
    if (!eventHandlers.message) return;
    var priority = getPriority(chaosDefinitions);
    var msg = randomItem(chaosDefinitions[priority])(faker);
    eventHandlers.message.forEach(function (cb) {
      return cb(msg);
    });
  };

  var poll = function poll() {
    emitRandomEvent();
    setInterval(emitRandomEvent, options.delay);
  };

  var stop = function stop() {};

  var on = function on(eventName, cb) {
    eventHandlers[eventName] = eventHandlers[eventName] || [];
    eventHandlers[eventName].push(cb);
  };

  return {
    poll: poll,
    stop: stop,
    on: on
  };
};